@import "../../assets/style/variables";
@import "../../assets/style/mixin";

.L-skills-wrapper {
  width       : 100%;
  min-height  : 100vh;
  height      : 100%;
  padding-top : 150px;

  @include screen-max-width-991 {
	padding-top : 150px;
  }


  .L-skills-container {
	margin    : 0 auto;
	position  : relative;
	max-width : 650px;
	width     : 90%;
	height    : 700px;
	@include screen-max-width-1200 {
	  max-width : 600px;
	  width     : 100%;
	  height    : 600px;
	}
	@include screen-max-width-991 {
	  max-width  : 550px;
	  width      : 100%;
	  max-height : 650px;
	}
	@include screen-max-width-576 {
	  max-height : 500px;
	  max-width  : 576px;
	  width      : 85%;
	  margin     : 0 auto;
	}

	.L-skills-box {
	  position                : absolute;
	  top                     : 0;
	  left                    : 0;
	  right                   : 0;
	  bottom                  : 0;
	  border-radius           : 25px;
	  background              : rgba(143, 143, 143, 0.03);
	  box-shadow              : 0 4px 30px rgba(0, 0, 0, 0.21);
	  backdrop-filter         : blur(13.8px);
	  -webkit-backdrop-filter : blur(13.8px);
	  @include screen-max-width-576 {
		height : 450px;
	  }

	  .L-logo-box {
		width                   : 85%;
		margin                  : 20px auto;
		padding                 : 15px 15px;
		height                  : 550px;
		border-radius           : 25px;
		overflow                : hidden;
		background              : rgba(255, 255, 255, 0.02);
		backdrop-filter         : blur(12.5px);
		-webkit-backdrop-filter : blur(12.5px);
		-moz-box-shadow         : inset 2px 20px 50px rgba(154, 147, 140, 0.5),
		1px 1px 50px rgba(255, 255, 255, 1);
		-webkit-box-shadow      : inset 2px 20px 50px rgba(154, 147, 140, 0.5),
		1px 1px 50px rgba(255, 255, 255, 1);
		box-shadow              : inset 2px 20px 50px rgba(154, 147, 140, 0.5),
		1px 1px 50px rgba(255, 255, 255, 1);


		@include screen-max-width-1200 {
		}
		@include screen-max-width-767 {
		  width : 90%;
		}
		@include screen-max-width-576 {
		  height : 300px;
		}

		.L-skill-box-items {
		  @include screen-max-width-1200 {
			width  : 300px;
			height : 250px;
			border : 1px solid red;
		  }
		}
	  }

	  .slider-big {
		text-align : center;
		margin     : 0 auto;
		max-width  : 400px;
		width      : 100%;


		.skill-animated-logo {
		  animation : rotateLogo 10s linear infinite;
		  position  : relative;

		  &:after {
			content       : "";
			border-bottom : 10px solid rgba(#000, 0.2);
			width         : 100%;
			position      : absolute;
			left          : 0;
			bottom        : -50px;
			border-radius : 50%;
			filter        : blur(100%);
			@include screen-max-width-1200 {
			  width : 50%;
			  left  : 25%;
			}
			@include screen-max-width-576 {
			  width : 50%;
			  left  : 25%;
			}
		  }

		  span {
			font-size : 300px;
			@include screen-max-width-1200 {
			  font-size : $font-size-200;
			}
			@include screen-max-width-991 {
			  font-size : $font-size-200;
			}

			@include screen-max-width-576 {
			  font-size : $font-size-150;
			}
		  }
		}

		.L-skill-name {
		  margin-top          : 100px;
		  width               : 100%;
		  font-size           : $font-size-36;
		  color               : $white;
		  -webkit-text-stroke : 1px $black;
		  @include screen-max-width-767 {
			margin-top : 80px;
			font-size  : $font-size-28;
		  }
		  @include screen-max-width-576 {
			margin-top : 70px;
		  }
		}
	  }

	  .slider-little {
		width  : 80%;
		margin : 50px auto;
		@include screen-max-width-576 {
		  margin : 20px auto;
		}

		.slick-list {
		  height     : 90px;
		  background : rgba(255, 255, 255, 0.01);
		  @include screen-max-width-440 {
			margin : 0;
			height : 70px;
		  }
		}

		span {
		  display                 : block;
		  padding                 : 15px;
		  width                   : max-content;
		  font-size               : $font-size-50;
		  cursor                  : pointer;
		  background              : rgba(255, 255, 255, 0.36);
		  border-radius           : 15px;
		  box-shadow              : 0 4px 30px rgba(0, 0, 0, 0.1);
		  backdrop-filter         : blur(10.4px);
		  -webkit-backdrop-filter : blur(10.4px);
		  transition              : $transition-3;
		  margin-right            : 15px;
		  @include screen-max-width-576 {
			font-size : $font-size-36;
		  }
		  @include screen-max-width-440 {
			font-size : $font-size-28;
		  }

		  &:hover {
			opacity    : 0.7;
			transition : $transition-3;
			transform  : translateY(10%);
		  }
		}
	  }
	}
  }
}

@keyframes rotateLogo {
  0% {
	transform  : rotateX(0deg) rotateY(0deg);
	transition : $transition-2 all;
  }
  50% {
	transform  : rotateX(0deg) rotateY(360deg);
	transition : $transition-2 all;
  }
  100% {
	transform  : rotateX(0deg) rotateY(0deg);
	transition : $transition-2 all;
  }
}
