.L-burger-wrapper {
	display: none;

	.L-burger {
		position: absolute;
		width: 40px;
		height: 35px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		flex-direction: column;
		left: 40px;
		top: 30px;
		cursor: pointer;

		.L-burger-line {
			width: 100%;
			height: 4px;
			background: $white;
			border-radius: 10px;
			transition: 0.3s;
		}

		.L-burger-active {
			justify-content: center;
		}

		.L-burger-active:nth-child(1) {
			position: absolute;
			transform: rotateZ(45deg);
		}

		.L-burger-active:nth-child(2) {
			position: absolute;
			width: 0px;
			height: 0px;
		}

		.L-burger-active:nth-child(3) {
			position: absolute;
			transform: rotateZ(-45deg);
		}
	}

	.L-burger-active {
		cursor: pointer;
		position: fixed;
		top: 20px;
		left: 20px;
		z-index: 9000;
	}

	@include screen-max-width-991 {
		display: block;
	}
}
.L-lng-font {
	font-family: Armenian !important;
}
